import { Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { SvgIcon } from "../../common/SvgIcon";
import Container from "../../common/Container";

import i18n from "i18next";
import {
  FooterSection,
  Title,
  NavLink,
  Extra,
  LogoContainer,
  Para,
  Large,
  Chat,
  Empty,
  FooterContainer,
  Language,
  Label,
  LanguageSwitch,
  LanguageSwitchContainer,
} from "./styles";

interface SocialLinkProps {
  href: string;
  src: string;
}

const Footer = ({ t }: any) => {
  const handleChange = (language: string) => {
    i18n.changeLanguage(language);
  };

  const SocialLink = ({ href, src }: SocialLinkProps) => {
    return (
      <a
        href={href}
        target="_blank"
        rel="noopener noreferrer"
        key={src}
        aria-label={src}
      >
        <SvgIcon src={src} width="25px" height="25px" />
      </a>
    );
  };

  return (
    <>
      <FooterSection>
        <Container>
          <Row justify="space-between">
            <Col lg={10} md={10} sm={12} xs={12}>
              <Language>{t("Contact")}</Language>

              <Para>
              <a href="tel:07506 559208">
              <Chat>{t(` 07506559208`)}</Chat>
              </a>
              </Para>
              <a href="mailto:thelakesdev@gmail.com">
                <Chat>{t(` Email me`)}</Chat>
              </a>
            </Col>

            <Col lg={10} md={10} sm={12} xs={12}>
              
              <Language>{t("Address")}</Language>
              
              <Para>2 Tilberthwaite Cottages,</Para>
              <Para>Tilberthwaite,</Para>
              <Para>Cumbria, LA21 8DG,</Para>
              <Para>United Kingdom</Para>
            </Col>
          </Row>
        </Container>
      </FooterSection>
      <Extra>
        <Container border={true}>
          <Row
            justify="space-between"
            align="middle"
            style={{ paddingTop: "3rem" }}
          >
            <NavLink to="/">
              <LogoContainer>
                <SvgIcon
                  src="logo.svg"
                  aria-label="homepage"
                  width="202px"
                  height="128px"
                />
              </LogoContainer>
            </NavLink>
           
              <SocialLink
                href="https://github.com/thelakesdev"
                src="github.svg"
              />
              <SocialLink
                href="https://twitter.com/thelakesdev"
                src="twitter.svg"
              />
              <SocialLink
                href="https://www.linkedin.com/in/thelakesdev/"
                src="linkedin.svg"
              />
              <SocialLink
                href="https://medium.com/@thelakesdev/"
                src="medium.svg"
              />
           
          </Row>
        </Container>
      </Extra>
    </>
  );
};

export default withTranslation()(Footer);
